'use client';

import { CardStackCarousel } from "@/components/CardStackCarousel";
import { BlackBackgroundCarousel } from "@/components/BlackBackgroundCarousel";
import { WhiteBackgroundCarousel } from "@/components/WhiteBackgroundCarousel";
import { motion, useAnimation, Variants } from "framer-motion";
import { useEffect, useRef } from "react";

// ✅ Section-level animation variants
const sectionVariants: Variants = {
  hidden: { opacity: 0, y: 30 },
  visible: {
    opacity: 1,
    y: 0,
    transition: {
      duration: 1.2,
      type: "spring", // ✅ now properly typed
      stiffness: 80,
      damping: 20,
      when: "beforeChildren",
      staggerChildren: 0.15,
    },
  },
};

// ✅ Poster-level animation variants
const posterVariants: Variants = {
  hidden: { opacity: 0, y: 20 },
  visible: {
    opacity: 1,
    y: 0,
    transition: {
      duration: 0.8,
      type: "spring", // ✅ correct union type
      stiffness: 70,
      damping: 20,
    },
  },
};

export default function CarouselPage() {
  const sections = [useRef(null), useRef(null), useRef(null)];
  const controls = [useAnimation(), useAnimation(), useAnimation()];

  useEffect(() => {
    const observers: IntersectionObserver[] = [];

    sections.forEach((sectionRef, index) => {
      const observer = new IntersectionObserver(
        ([entry]) => {
          if (entry.isIntersecting) {
            controls[index].start("visible");
          } else {
            controls[index].start("hidden");
          }
        },
        { threshold: 0.3 }
      );
      if (sectionRef.current) observer.observe(sectionRef.current);
      observers.push(observer);
    });

    return () => observers.forEach((obs) => obs.disconnect());
  }, []);

  return (
    <div className="h-screen w-full overflow-y-scroll snap-y snap-mandatory">
      {/* Card Stack */}
      <motion.section
        ref={sections[0]}
        className="h-screen snap-start flex items-center justify-center"
        initial="hidden"
        animate={controls[0]}
        variants={sectionVariants}
      >
        <motion.div variants={posterVariants} className="w-full h-full flex items-center justify-center">
          <CardStackCarousel />
        </motion.div>
      </motion.section>

      {/* Black Background */}
      <motion.section
        ref={sections[1]}
        className="h-screen snap-start flex items-center justify-center"
        initial="hidden"
        animate={controls[1]}
        variants={sectionVariants}
      >
        <motion.div variants={posterVariants} className="w-full h-full flex items-center justify-center">
          <BlackBackgroundCarousel />
        </motion.div>
      </motion.section>

      {/* White Background */}
      <motion.section
  ref={sections[0]}
  className="h-screen snap-start flex items-center justify-center bg-black" // keep background solid
  initial="visible" // section itself is always visible
  animate="visible"
>
  <motion.div
    variants={sectionVariants}
    initial="hidden"
    animate={controls[0]}
    className="w-full h-full flex items-center justify-center"
  >
    <CardStackCarousel />
  </motion.div>
</motion.section>

    </div>
  );
}
